import React, { Component } from 'react';
import {} from 'prop-types';
import Overlay from '../UI/Overlay';

function sendPulse(email) {
  const params = {
    callback: 'onSubscribeResponse',
    'sform[email]': email,
    '': '',
    sform_lang: 'ru',
    'sform[hash]': '5282c330c9f7c6a127f710619146a2659e7cdac1364771397b45e43c555ffd02',
    [`sform[${btoa('autoSite')}]`]: 'potofcode.com',
    _: Date.now(),
  };
  const query = Object.entries(params).map(([key, value]) => encodeURIComponent(key) + '=' + encodeURIComponent(value)).join('&');
  const src = `https://login.sendpulse.com/members/forms/jsonp-submit?${query}`;
  const script = document.createElement('script');
  script.src = src;
  return new Promise(resolve => {
    script.onload = () => {
      script.parentNode.removeChild(script);
      resolve();
    };
    document.body.appendChild(script);
  });
}

function mailChimp() {
  return async (EMAIL) => {
    const params = {
      'mc-embedded-subscribe-form[u]': '2086f3a4bc22e651ff2d7721b',
      'mc-embedded-subscribe-form[amp;id]': '731984f7af',
      'mc-embedded-subscribe-form[b_2086f3a4bc22e651ff2d7721b_731984f7af]': '',
      'mc-embedded-subscribe-form[subscribe]': 'Subscribe',
    };

    // https://potofcode.us16.list-manage.com/subscribe/post
  };
}

export default class SubscriptionForm extends Component {

  state = {
    email: '',
    display: false,
  };

  onSubscribeResponse = (...args) => {
    debugger;
  };

  onSubscribe = event => {
    event.preventDefault();
    const { email } = this.state;
    sendPulse(email);
  };

  onEmailChange = ({ target: { value } }) => {
    this.setState({ email: value });
  };

  onCloseRequest = () => {
    this.setState({ display: false });
  };

  componentDidMount() {
    window.onSubscribeResponse = this.onSubscribeResponse;
    this.setState({ display: true });
  }

  render() {
    const { display, email } = this.state;
    return (
      <Overlay display={display} onCloseRequest={this.onCloseRequest} transition="slide-up">
        <div className="subscribe">
          <div className="subscribe__container">
            <div className="subscribe__close" onClick={this.onCloseRequest}>close</div>
            <div className="subscribe__title">
              <div className="ribbon">
                <div className="ribbon-stitches-top" />
                <div className="ribbon-content">
                  <h1><b>
                    Pot of Code<br />
                    еженедельный дайджест<br />
                  </b></h1>
                </div>
                <div className="ribbon-stitches-bottom" />
              </div>
            </div>
            <input
              type="email"
              className="subscribe__email"
              placeholder="username@gmail.com"
              value={email}
              onChange={this.onEmailChange}
              autoFocus={true}
              required="required"
            />

            <button
              className="subscribe__button g-recaptcha"
              data-sitekey="6LcdwTgUAAAAADebBBV1FR0FExLnvgtC2F3Mofrx"
              data-callback="YourOnSubmitFn"
              onClick={this.onSubscribe}
            >Подписаться</button>
          </div>
        </div>
      </Overlay>
    );
  }
}



