import React, { Component } from 'react';
import { bool, func, number } from 'prop-types';
import Helmet from 'react-helmet';
import classNames from 'classnames';
import EventListener from 'react-helpful/EventListener';
import CSSTransition from '../Transitions/CSSTransition';

export default class Overlay extends Component {
  static propTypes = {
    display: bool.isRequired,
    onCloseRequest: func.isRequired,
    timeout: number,
  };

  static defaultProps = {
    timeout: 375,
  };
  
  onClick = event => {
    if (event.target === event.currentTarget) {
      this.props.onCloseRequest();
    }
  };

  onKeyUp = event => {
    if (event.key === 'Escape') {
      this.props.onCloseRequest();
    }
  };

  render() {
    const { display, children, className, transition, timeout } = this.props;
    if (!display) {
      return null;
    }
    return (
      <div className={classNames('overlay', className)} onClick={this.onClick}>
        <CSSTransition in={display} timeout={timeout} classNames={transition}>
          {children}
        </CSSTransition>
        <Helmet>
          <body className="viewer--no-scroll" />
        </Helmet>
        <EventListener
          target={() => document}
          event="keyup"
          on={this.onKeyUp}
        />
      </div>
    );
  }
}



